var REQUEST = require('../lib/request');
var util = require('./util');


// Bucket 

/**
 *  bucket 
 * @param  {Object}  params         
 * 
 * @param  {Function}  callback     
 */
function getService(params, callback) {

    if (typeof params === 'function') {
        callback = params;
        params = {};
    }
    var protocol = this.options.Protocol || (util.isBrowser && location.protocol === 'http:' ? 'http:' : 'https:');
    var domain = this.options.ServiceDomain;
    var region = params.Region;
    if (domain) {
        domain = domain.replace(/\{Region\}/ig, region || '').replace(/\{.*?\}/ig, '');
        if (!/^[a-zA-Z]+:\/\//.test(domain)) {
            domain = protocol + '//' + domain;
        }
        if (domain.slice(-1) === '/') {
            domain = domain.slice(0, -1);
        }
    } else if(region){
        domain = protocol + '//cos.'+ region + '.myqcloud.com';
    } else {
        domain = protocol + '//service.cos.myqcloud.com';
    }

    submitRequest.call(this, {
        Action: 'name/cos:GetService',
        url: domain,
        method: 'GET',
        headers: params.Headers,
    }, function (err, data) {
        if (err) {
            return callback(err);
        }
        var buckets = (data && data.ListAllMyBucketsResult && data.ListAllMyBucketsResult.Buckets
            && data.ListAllMyBucketsResult.Buckets.Bucket) || [];
        buckets = util.isArray(buckets) ? buckets : [buckets];
        var owner = (data && data.ListAllMyBucketsResult && data.ListAllMyBucketsResult.Owner) || {};
        callback(null, {
            Buckets: buckets,
            Owner: owner,
            statusCode: data.statusCode,
            headers: data.headers,
        });
    });
}

/**
 *  Bucket
 * @param  {Object}  params                         
 *     @param  {String}  params.Bucket              Bucket
 *     @param  {String}  params.Region              
 *     @param  {String}  params.ACL                 privatepublic-readprivate
 *     @param  {String}  params.GrantRead           x-cos-grant-read: uin=" ",uin=" "
 *     @param  {String}  params.GrantWrite          x-cos-grant-write: uin=" ",uin=" "
 *     @param  {String}  params.GrantFullControl    x-cos-grant-full-control: uin=" ",uin=" "
 * @param  {Function}  callback                     
 * @return  {Object}  err                           https://cloud.tencent.com/document/product/436/7730
 * @return  {Object}  data                          
 *     @return  {String}  data.Location             
 */
function putBucket(params, callback) {

    var self = this;
    submitRequest.call(this, {
        Action: 'name/cos:PutBucket',
        method: 'PUT',
        Bucket: params.Bucket,
        Region: params.Region,
        headers: params.Headers,
    }, function (err, data) {
        if (err) {
            return callback(err);
        }
        var url = getUrl({
            protocol: self.options.Protocol,
            domain: self.options.Domain,
            bucket: params.Bucket,
            region: params.Region,
            isLocation: true,
        });
        callback(null, {
            Location: url,
            statusCode: data.statusCode,
            headers: data.headers,
        });
    });
}

